import React, { useEffect, useState } from "react";
import { Tab, Tabs, Row, Nav, Col } from 'react-bootstrap';
import TablePg from "./tablePg";




const Main = () => {

    const [key, setKey] = useState('assets');

    return (
        <div className="p-3">
            <br />
            <h1 class="text-left">Pharmablock</h1>
            <br />
            <Tabs
                id="controlled-tab-example"
                activeKey={key}
                onSelect={(k) => setKey(k)}
                className="mb-3"
            >
                <Tab eventKey="assets" title="Assets">


                    <Tab.Container id="left-tabs-example" defaultActiveKey="first">
                        <Row>
                            <Col sm={2}>
                                <Nav variant="pills" className="flex-column">
                                    <Nav.Item>
                                        <Nav.Link eventKey="first">Goods</Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="second">Letter of Credit</Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="third">Bill of Landing</Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="fourth">Regulatory Accredation</Nav.Link>
                                    </Nav.Item>
                                </Nav>
                            </Col>
                            <Col sm={9}>
                                <Tab.Content>
                                    <Tab.Pane eventKey="first">
                                        <TablePg title="Goods" headings={["ID", "Temperature", "Humidity", "CurrentOwner", "Delivered"]} data={[
                                            [
                                                1, 20, 5, "owner1", "false"
                                            ],
                                            [
                                                2, 22, 8, "owner2", "false"
                                            ]
                                        ]} />
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="second">
                                        <TablePg title="Letter of Credit" headings={["DocumentID", "Amount", "Quantity", "IssueDate", "DateOfExpiry", "CurrentOwner"]} data={[
                                            [
                                                1, 20, 5, "01/08/2021", "01/11/2021", "Importer Bank"
                                            ],
                                            [
                                                2, 20, 5, "01/08/2021", "01/11/2021", "Importer Bank"
                                            ]
                                        ]} />

                                    </Tab.Pane>
                                    <Tab.Pane eventKey="third">
                                        <TablePg title="Bill of Landing" headings={["DocumentID", "Amount", "Quantity", "CurrentOwner"]} data={[
                                            [
                                                1, 20, 5, "Exporter"
                                            ],
                                            [
                                                2, 20, 5, "Exporter"
                                            ]
                                        ]} />
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="fourth">
                                        <TablePg title="Regulatory Accredation" headings={["Authority", "Verified"]} data={[
                                            [
                                                "Authority1", "false"
                                            ],
                                            [
                                                "Authority1", "false"
                                            ]
                                        ]} />
                                    </Tab.Pane>
                                </Tab.Content>
                            </Col>
                        </Row>
                    </Tab.Container>



                </Tab>
                <Tab eventKey="participants" title="Participants">

                    <Tab.Container id="left-tabs-example" defaultActiveKey="first">
                        <Row>
                            <Col sm={2}>
                                <Nav variant="pills" className="flex-column">
                                    <Nav.Item>
                                        <Nav.Link eventKey="first">Importer</Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="second">Exporter</Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="third">Importer Bank</Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="fourth">Exporter Bank</Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="fifth">Regulatory Authority</Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="sixth">Shipping Company</Nav.Link>
                                    </Nav.Item>
                                </Nav>
                            </Col>
                            <Col sm={9}>
                                <Tab.Content>
                                    <Tab.Pane eventKey="first">
                                        <TablePg title="Importer" headings={["StaffID"]} data={[
                                            [
                                                1
                                            ],
                                            [
                                                2
                                            ],
                                            [
                                                3
                                            ]
                                        ]} />
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="second">
                                        <TablePg title="Exporter" headings={["StaffID", "HasAccepted"]} data={[
                                            [
                                                1, "false"
                                            ],
                                            [
                                                2, "false"
                                            ]
                                        ]} />

                                    </Tab.Pane>
                                    <Tab.Pane eventKey="third">
                                        <TablePg title="Importer Bank" headings={["BankID", "Balance", "CreditScore", "CountryRisk"]} data={[
                                            [
                                                1, 20, 5, 80
                                            ],
                                            [
                                                2, 20, 5, 95
                                            ]
                                        ]} />
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="fourth">
                                        <TablePg title="Exporter Bank" headings={["BankID"]} data={[
                                            [
                                                "Bank1"
                                            ],
                                            [
                                                "Bank2"
                                                
                                            ]
                                        ]} />
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="fifth">
                                        <TablePg title="Regulatory Authority" headings={["ID"]} data={[
                                            [
                                                1
                                            ],
                                            [
                                                2
                                            ]
                                        ]} />
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="sixth">
                                        <TablePg title="Shipping Company" headings={["ID"]} data={[
                                            [
                                                1
                                            ],
                                            [
                                                2
                                            ]
                                        ]} />
                                    </Tab.Pane>
                                </Tab.Content>
                            </Col>
                        </Row>
                    </Tab.Container>




                </Tab>
                <Tab eventKey="transactions" title="Transactions">
                    <h1>oneee</h1>
                </Tab>

            </Tabs>
        </div>

    )
}

export default Main;

