import { Button, Modal } from 'react-bootstrap';
import React, { useReducer, useState } from "react";
import {FaEdit, FaTrashAlt} from 'react-icons/fa';
import AddForm from './AddForm';

const TablePg = (props) => {

   
    const [show, setShow] = useState(false);
    const [data, setData] = useState(props.data);

    const handleClose = () => {setShow(false); forceUpdate();}
    const handleShow = () => setShow(true);

    const [ignored, forceUpdate] = useReducer(x => x + 1, 0);

    console.log(data)

    return (
        <div>
            <div className="row">
                <div className="col-9">
                    <h1>{props.title}</h1>
                </div>
                <div className="col-3">
                    <Button variant="primary" onClick={handleShow}>
                        Add {props.title}
                    </Button>
                </div>
            </div>  <br /> <br />

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title> Add {props.title}</Modal.Title>
                </Modal.Header>
                <Modal.Body>

              <AddForm headings={props.headings} data={props.data} hc={handleClose} title={props.title} sd={setData}/>
                </Modal.Body>

                <Modal.Footer>
                    
                </Modal.Footer>
            </Modal>


                <table class="table">
                    <thead>
                        <tr>
                            {props.headings.map((headings) => {
                                return (<th scope="col">{headings}</th>)
                            })}
                            <th>Actions</th>
                        </tr>
                    </thead>

                    <tbody>

                        {data.map((product,i) => {

                            return (
                                <tr>

                                    {product.map((item) => {
                                        return (<td>{item}</td>)
                                    })
                                    }
                                    <td><FaEdit/>&nbsp;&nbsp;&nbsp;&nbsp;<FaTrashAlt onClick={() => {data.splice(i,1); console.log(data); setData(data); forceUpdate();}}/></td>
                                </tr>
                            )
                        })}


                    </tbody>
                </table>
        </div>
            )
}

            export default TablePg;

